import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FaEnvelope,
  FaExchangeAlt,
  FaLock,
  FaPlus,
  FaRemoveFormat,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";

import { ImTextColor } from "react-icons/im";
import { ImListNumbered } from "react-icons/im";

import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./DetailPlanEdit.css";

import { RoutesString } from "pages/routesString";
import DetailPlanAPI from "api/common/detail-plan";
import TeachingPlanAPI from "api/common/teaching-plan";
import DetailGroupAPI from "api/common/detail-plan";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import { number } from "yup/lib/locale";
import i18n from "i18n/i18n";
import subMajorApi from "api/common/subMajor";
import detailGroupAPI from "api/common/detail-group";
import { isErrors } from "utils/utils";

export default function DetailPlanEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    idKHGD: null,
    idCTGN: null,
    ghiChu: null,
    // isDeleted: false,
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [khgd, setKHGD] = useState([]);
  const [ctgn, setCTGN] = useState([]);

  const [subMajorId, setSubMajorId] = useState(null);
  const [listSubMajors, setListSubMajors] = useState([]);

  const getListSubMajors = async () => {
    try {
      const listSubMajorRes = await subMajorApi.getAll();
      if (listSubMajorRes) {
        setListSubMajors(listSubMajorRes.contents);
      }
    } catch (err) {
      setListSubMajors([])
    }
  }

  useEffect(() => {
    (async () => {
      await getListSubMajors();
    })()
  }, []);

  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
  }, []);

  useEffect(async () => {
    try {
      let query = {};
      if(subMajorId){
        query = {CTNganhDaoTao: subMajorId} 

        const ctgnRes = await detailGroupAPI.getAllCTNDT(subMajorId);
        setCTGN(ctgnRes.contents);
      }
      const khgdRes = await TeachingPlanAPI.getAll(query);
      setKHGD(khgdRes.contents);
    } catch (err) {// eslint-disable-next-line
    }
  }, [subMajorId]);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await DetailPlanAPI.getById(id);
      setInitialValues({
        idKHGD: res.idKHGD !== undefined && res?.idKHGD?.id,
        idCTGN: res.idCTGN !== undefined && res?.idCTGN?.id,
        ghiChu: res.ghiChu,
        // isDeleted: res.isDeleted,
      });
      if(res?.idKHGD?.nganhDaoTao){
        setSubMajorId(res?.idKHGD?.nganhDaoTao?.id)
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values) {
    values.idKHGD = parseInt(values.idKHGD, 10);
    values.idCTGN = parseInt(values.idCTGN, 10);
    // eslint-disable-next-line
    console.log(values);
    if (isUpdatePage) {
      const { ...body } = values;
      try {
        const res = await DetailPlanAPI.putById(id, body);
        if (res.status === 200) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const {  ...body } = values;
      try {
        const res = await DetailPlanAPI.post(body);
        if (res.id) {
          toast.success(t("admin.ADD_SUCCESS"));
          history.push(`${RoutesString.ADMIN_DETAIL_PLAN_EDIT}?id=${res.id}`);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await DetailPlanAPI.deleteById(id);
      if (res.status === 200) {
        setNotification({
          color: "success",
          isOpen: true,
          messages: [t("admin.common.DELETED")],
        });
        updateForm(id);
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.detail_teaching_plan.UPDATE_DETAIL_PLAN")
            : t("admin.detail_teaching_plan.ADD_DETAIL_PLAN")
        }
        items={[
          {
            href: RoutesString.ADMIN_DETAIL_PLAN_LIST,
            title: t("admin.detail_teaching_plan.DETAIL_PLAN_LIST"),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t("admin.detail_teaching_plan.UPDATE_DETAIL_PLAN")
            : t("admin.detail_teaching_plan.ADD_DETAIL_PLAN")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            idKHGD: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            idCTGN: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
          })}
        >
          {({ values, errors, dirty, isValid, handleChange, handleReset }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />

              {<FormGroup className="row">
                <Label className="col-lg-3">
                  {t("SCHOOL_YEAR")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      value={subMajorId}
                      onChange={e => setSubMajorId(e?.target?.value)}
                    >
                      {listSubMajors &&
                        listSubMajors.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {`${item?.khoa}-${item?.nganhDaoTao?.maNganhDaoTao}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>}{" "}
              <br />

              {subMajorId ? <FormGroup className="row">
                <Label for="idKHGD" className="col-lg-3">
                  {t("admin.detail_teaching_plan.TEACHING_PLAN")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idKHGD"
                      value={values.idKHGD}
                      onChange={handleChange}
                    >
                      {khgd &&
                        khgd.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {`${item.id}-${item?.nganhDaoTao?.nganhDaoTao?.maNganhDaoTao}-${item?.nganhDaoTao?.khoa}-${i18n.t('admin.detail_teaching_plan.SEMESTER_NAME')} ${item?.tenHocKy}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup> : ""}{" "}
              <br />

              {subMajorId ? <FormGroup className="row">
                <Label for="idCTGN" className="col-lg-3">
                  {t("admin.detail_teaching_plan.DETAIL_GROUP")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idCTGN"
                      value={values.idCTGN}
                      onChange={handleChange}
                    >
                      {ctgn &&
                        ctgn.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.id}-{item?.monHoc?.tenTiengViet}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>: ''}{" "}
              <br />

              {subMajorId ? <FormGroup className="row">
                <Label for="ghiChu" className="col-lg-3">
                  {t("admin.detail_teaching_plan.NOTE")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <ImTextColor />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ghiChu"
                      value={values.ghiChu}
                      onChange={handleChange}
                      // invalid={errors.ghiChu}
                      // readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ghiChu && (
                    <FormText color="danger">{errors.ghiChu}</FormText>
                  )}
                </div>
              </FormGroup>: ''}{" "}
              <br />

              {/* {isUpdatePage && (
                <>
                  <FormGroup className="row">
                    <Label for="isDeleted" className="col-lg-3">
                      {t("admin.common.AVAILABLE_STATUS")}
                    </Label>
                    <div className="col-lg-6">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaRemoveFormat />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="isDeleted"
                          value={values.isDeleted}
                          onChange={handleChange}
                        >
                          {availableStatus &&
                            availableStatus.map((item, index) => {
                              return (
                                <option key={index} value={item.value}>
                                  {item.name}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                </>
              )} */}

              <FormGroup>
                <Button
                  color="success"
                  disabled={!(isValid && dirty)}
                  onClick={() => handleEditOnClick(values)}
                  disabled={isErrors(errors)}
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
